import { Link } from "gatsby";
import styled from "styled-components";
import { device } from "../../styles/themes/device";
const StyledNavBar = styled.nav`
  margin-top: 1px;
  height: 2.5rem;
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
  border-bottom: 0.1rem solid var(--color-1);
  box-sizing: content-box;
  font-size: 0.85rem;

  .nickname {
    min-width: 12rem;
    justify-content: left;
  }

  & > * {
    padding: 1rem 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;

    border-right: 0.1rem solid var(--color-1);
    border-left: 0.1rem solid var(--color-1);

    height: 100%;

    text-align: center;
    color: var(--text-unfocused);
  }
  & > * {
    border-left: 0;
    border-top: 0;
  }

  .contact {
    white-space: nowrap;
    margin-left: auto;
    .mail {
      text-decoration: none;
      color: var(--text-unfocused);
      

      &:hover {
        color: var(--text-focused);
      }
    }
  }
  .link {
    position: relative;
    min-width: 100px;

    text-decoration: none;
    &::before {
      content: "_";
    }
    &:active {
      color: var(--text-focused);
    }
    &.selected {
      color: var(--text-focused);
      &::after {
        content: "";
        position: absolute;
        background-color: orange;
        border-radius: 6px;
        right: 0;
        bottom: 0;
        height: 2px;
        width: 100%;
      }
    }
  }
  .contact-dropdown,
  .dropdown {
    display: none;
    visibility: hidden;
  }

  @media ${device.tablet} {
    position: relative;
    z-index: 2;

    width: inherit;
    font-size: 1.4rem;

    /* outline: var(--color-1) solid 3px; */

    & > *:not(:first-child) {
      display: none;
    }

    .nickname {
      width: 100%;

      display: flex;
      text-align: left;
      justify-content: space-between;
    }

    .dropdown {
      display: block;
      visibility: visible;
    }
    .link {
      &.selected {
        &::after {
          all: unset;
        }
      }
    }

    //for true dropdown
    //.dropped
    &.dropped {
      flex-direction: column;

      & > *:not(:first-child) {
        visibility: visible;
        width: 100%;
        border: 1px solid var(--color-1);
        text-align: left;

        vertical-align: middle;
        display: flex;
        justify-content: start;
      }
      .contact {
        visibility: hidden;
        display: none;
      }
    }
  }
`;

export { StyledNavBar };
